<template>
  <div id="modelOutPutPanel">
    <ul class="buttons">
      <li>
        <el-button @click="saveDiagram()" type="primary" icon="el-icon-download">bpmn</el-button>
      </li>
      <li>
        <el-button @click="saveSvg()" type="primary" icon="el-icon-download">svg</el-button>
      </li>
      <li>
        <el-button @click="saveToBackend()" type="primary" icon="el-icon-upload2"></el-button>
      </li>
      <!-- <li>
        <el-input placeholder="" v-model="modelOutputBackendUrl" style="width:200px">
          <template slot="prepend">Http://</template>
        </el-input>
        <el-button @click="saveToUrl()" type="primary" icon="el-icon-cloudy"></el-button>
      </li>-->
    </ul>
    <a ref="localDownLink" href="javascript:" title></a>
  </div>
</template>
<script>
import SysLinks from "@/classes/z-server-vue/core/FrontLinkSysParamInf";
import BpmnInfs from "@/classes/bpmn/BpmnInfs";
export default {
  props: {
    model: Object
  },
  mounted() {},
  data() {
    return { diagramName: "diagram", modelOutputBackendUrl: "" };
  },
  methods: {
    saveToBackend() {
      console.log(this.$root.rootValue);
      if (this.modelOutputBackendUrl == "") {
        this.modelOutputBackendUrl = SysLinks.getSysLink().sysParamOutUrl;
      }
      console.log(this.modelOutputBackendUrl);
    },
    saveToUrl() {
      this.saveToBackend();
    },
    // SVG,done
    saveSvg() {
      var diagramName = this.model.getName();
      this.setEncoded(diagramName + ".svg", this.model.modelSvg);
    },
    // bpmn,done
    saveDiagram() {
      var diagramName = this.model.getName();
      this.setEncoded(diagramName + ".bpmn", this.model.modelXml);
    },
    setEncoded(name, data) {
      var link = this.$refs.localDownLink;
      // xmlURI
      const encodedData = encodeURIComponent(data);
      // ,aclassNameahrefdownload
      // console.log(name, data);
      // let xmlFile = new File([data], "test.bpmn");
      // console.log(xmlFile);
      if (data) {
        // link.className = "active";
        link.href = "data:application/bpmn20-xml;charset=UTF-8," + encodedData;
        link.download = name;
        link.click();
      }
    }
  }
};
</script>
<style  >
.buttons {
}
.buttons li {
  display: inline-block;
  margin: 5px;
}
</style>